body {
  margin: 0;
}


.card-front {
  
  height:120pt;
  width: 100%;
  overflow: hidden;
  padding: 5pt;
  margin: 5pt;
  color: white;
}

.card-front-inner{
  background-color :  rgb(19, 152, 170);
  height: 100%;
  padding-left: 2pt;
  padding-right: 2pt;
  color: white;
}

.arrow-b{

  cursor: pointer;
  position:fixed;
  top:50%;
  float: center;
  padding: 20pt;
  background-color :  rgb(13, 201, 220);
  color: white;
  border-radius: 0px 30px 30px 0px;
}

.arrow-a{

  cursor: pointer;
  position:fixed;
  top:50%;
  float: center;
  padding: 20pt;
  border-radius: 30px 0px 0px 30px;
  background-color :  rgb(13, 201, 220);
  color: white;
}

.arrow-b:hover{
  background-color: rgb(12, 125, 139);
}

.module-sticker{

  position: fixed;  
  width: 100%;
}
.module-positioner{
  position: fixed;
  top:50%;
  padding: 5pt;
  margin: 5pt;
  width: 100%;
  background-color: rgb(13, 201, 220);  
  
}

.module-fixed{
  position: fixed;
}
.arrow-a:hover{
  background-color: rgb(12, 125, 139);
}
.card-button-b{

  cursor: pointer;
  position: relative;
  bottom: 35pt;
  float: bottom

}
.card-button-f{
  cursor: pointer;
  position: relative;
  bottom: 35pt;
  float: bottom

}

.card-back {
  padding-top: 3pt;
  background-color :  rgb(19, 152, 170);
  height:100pt;
  width: 100%;
  cursor: pointer;
  overflow: hidden;
  padding: 5pt;
  margin: 5pt;
  color: white;
}
.card-container{
  height: 75%;
  margin: auto
}

.card-dis{
  background-color : rgb(172, 169, 169);
  /* height: 40pt;
  width: 100%; */
  cursor: pointer;
  /* overflow: hidden; */
  padding: 7pt;
  margin: 7pt;
}


.sm-grid-left{
  /* margin: 20pt 5pt 0 5pt; */
  padding: 10pt 10pt 0pt 20pt;
}
.sm-grid-md{
  padding: 10pt 10pt 0pt 10pt;
}
.sm-grid-right{
  padding: 10pt 20pt 0pt 10pt;
}
.module-clicked{
  background-color: rgb(12, 75, 83);
  color:  white;
  padding: 5pt;
  margin: 5pt;
  cursor: pointer;

}
.module-unclicked{
  padding: 5pt;
  margin: 5pt;
  background-color: rgb(19, 152, 170);
  color:  white;
  cursor: pointer;
}

.card-clicked{
  background-color: rgb(12, 75, 83);
  color:  white;
  padding: 5pt;
  margin: 5pt;
  cursor: pointer;

}
.card-unclicked{
  padding: 5pt;
  margin: 5pt;
  color: white;
  background-color : rgb(19, 152, 170);
  cursor: pointer;
}
hr{
  color: white
}